import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,ViewController, Events} from 'ionic-angular';
import { ContactProvider } from './../../../providers/contact-provider'; 

/**
 * Generated class for the ContactMoreModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contact-more-modal',
  templateUrl: 'contact-more-modal.html',
})
export class ContactMoreModalPage {

  latestFriends: any;
  newFriendsRequest: number = 0;

  parentCallback: {openContactAddPage: () => void, openContactGroupManage: () => void, openBlackListManage: () => void };
  constructor(public navCtrl: NavController,
    public contactProvider: ContactProvider,
    public viewCtrl:ViewController,
     public navParams: NavParams,
     public events: Events) {
      this.parentCallback = this.navParams.data.callback;
    // this.LatestFriendRequest();

    
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad ContactMoreModalPage');
  }
  openContactAddPage(){
    this.parentCallback.openContactAddPage()
    this.viewCtrl.dismiss(); 
    
  } 
  LatestFriendRequest(){
    this.contactProvider.LatestFriendRequest().subscribe(res=>{
      this.latestFriends = res.result;
      for(let item of this.latestFriends){
        if(item.status == 'waiting'){
          this.newFriendsRequest ++;
        }
      }
    })
  }
  openContactGroupManage(){
    this.parentCallback.openContactGroupManage()
    this.viewCtrl.dismiss();
  }
  openBlackListManage(){
    this.parentCallback.openBlackListManage()
    this.viewCtrl.dismiss();
  }
 

}



